.subnav-menu {
    #open-action {
        margin-left: 1em;
        background: var(--accent);
        padding: 5px 15px;
        font-weight: 600;
        border: var(--border-line);
        font-size: .895em;
        @media screen and (min-width: 1024px) {
            display: none; } } }

.subnav.modal {
    position: fixed;
    z-index: 10000000;
    width: 100vw;
    height: 100vh;
    background: rgba( 0, 0, 0, .5);
    top: 0;
    left: 0;
    display: grid;
    align-items: center;
    justify-content: center;
    .context {
        display: block;
        width: 480px;
        height: auto;
        background: var(--bg);
        margin: auto;
        text-align: center;
        z-index: 100000001;
        border-radius: 10px;
        padding-inline-start: 0;
        @media screen and (max-width: 480px) {
            width: 80%; }
        li {
            list-style: none;
            border-bottom: var(--border-line);
            margin: 0;
            padding: 1.1em 1.5em;
            font-weight: 600;
            font-size: .92em;
            text-transform: capitalize; } } }



