.news.canal {
    ul {
        padding: 0; }
    ul li {
        list-style: none; }
    .feat {
        border-bottom: var(--border-line);
        @media screen and (max-width: 672px) {
            grid-gap: 0em;
            border-bottom: 00; }
        .thumb {
            grid-column: 4 / span 2;
            @media screen and (max-width: 1024px) {
                grid-column: 2 / span 1; }
            @media screen and (max-width: 672px) {
                grid-column: 1 / span 1; } }
        .thumb img {
            aspect-ratio: 8 / 4.5; }
        .post-title {
            font-size: 1.8em;
            font-weight: 600; }
        .excerpt {
            @media screen and (max-width: 1024px) {
                margin: 0; } } }
    .list {
        grid-gap: 1em;
        @media screen and (max-width: 1110px) {
            display: grid;
            grid-template-columns: auto auto; }
        @media screen and (max-width: 640px) {
            grid-template-columns: auto; }
        .context {
            width: 100%; }
        .item {
            padding-right: 1em;
            border-right: var(--border-line);
            @media screen and (max-width: 1110px) {
                border-right: 0; }
            @media screen and (max-width: 1110px) {
                border-bottom: var(--border-line); } }
        .item:nth-last-child(1) {
            border-right: 0;
            padding-right: 0em;
            @media screen and (max-width: 1110px) {
                padding-right: 1em; }
            @media screen and (max-width: 640px) {
                border-bottom: 0; } }
        .meta {
            width: calc( 100% - 93px ); }
        .thumb {
            width: 93px; }
        .thumb img {
            aspect-ratio: 1 / 1; } } }
